import { Section } from "../components/section";
import { DynamicStat, StaticStat } from "../components/stat";
import { Stats, FontPackageVersion } from "../data-import";
import { joinCls } from "../utils/join-classes";

export function Banner() {
    return (
        <Section className="banner">
            <h1 className={joinCls("banner-title")}>Iosevka</h1>
            <div className="banner-stats">
                <StaticStat quantity={FontPackageVersion} description="Version" />
                <a
                    className={joinCls("install-button")}
                    href="https://github.com/be5invis/Iosevka/releases"
                >
                    Download
                </a>
                <a
                    className={joinCls("install-button")}
                    href="https://github.com/be5invis/Iosevka"
                >
                    Visit Repository
                </a>
            </div>
            <div className="banner-scroll-down-arrow">
                <span></span>
                <span></span>
                <span></span>
            </div>
        </Section>
    );
}
